<!--  -->
<template>
  <div class="child_view">
    <mv-list :keyWord="keyWord" :mvData="mvData"></mv-list>
  </div>
</template>

<script>
import { HttpManager } from '../../api';

import MvList from '../../components/MvList.vue';

export default {
  name: "SearchMv",
  data () {
    return {

      keyWord: this.$route.query.key,
      mvData: {},
    };
  },

  components: {
    MvList
  },

  methods: {
    getMvList (val) {
      var that = this;
      HttpManager.getSearchByKeyWord("Mv", { key: this.keyWord, pn: val, rn: 30 })
        .then((res) => {
          that.mvData = Object.assign({}, that.mvData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              mvList: res.data.mvlist,
              pageSize: 30,
              getData: that.getMvList,
            })
          // console.log(res.code);
          // that.code = parseInt(res.code)
          // that.total = parseInt(res.data.total);
          // that.mvList = res.data.mvlist;
        })
        .catch((error) => {
          console.log(error);
        });
    },

  },
  computed: {
  },
  watch: {
    "$route.query.key": {
      handler (newVal, oldVal) {
        this.keyWord = newVal;
        this.getMvList(1);
      },
      immediate: true, //监听首次绑定值
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () { },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.mvs {
  .play_cnt {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 3px 5px;
    background-color: rgba(2, 2, 2, 0.3);
    color: #fff;
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    z-index: 10;
  }
  .name {
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    margin-top: 10px;
    display: block !important;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    span {
      cursor: pointer;
    }
  }
  .artist {
    color: #999;
    font-weight: 300;
    margin-top: 5px;
    margin-bottom: 26px;
    font-size: 14px;
    line-height: 1;
    font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
      sans-serif;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
}
</style>
<style lang="scss">
.mvs {
  .item {
    .cover_out {
      padding-bottom: 59% !important;
    }
  }
}
</style>



